<template>
  <div id="app" ref="app">
    <div class="progress-container" v-show="!assetsReady">
      <b-progress :value="assetsLoaded * 100 / 386" size="is-medium"></b-progress>
    </div>
    <div class="app-container" v-show="assetsReady">
      <div class="menu-container">
        <img class="left" src="../src/assets/images/logo.svg">
        <div class="mid"><span>{{timeMinSec[0]}}</span>:<span>{{timeMinSec[1]}}</span></div>
        <div class="right">
          <div class="buttons">
            <b-button type="is-primary" icon-right="undo-alt" v-on:click="reset" title="Reset"></b-button>
            <b-button type="is-info" icon-right="volume-up" title="Mute" v-show="!muted" v-on:click="mute" :disabled="isios"></b-button>
            <b-button type="is-info" icon-right="volume-mute" title="Unmute" v-show="muted" v-on:click="unmute" ></b-button>
            <b-button type="is-warning" icon-right="info" title="Info" v-on:click="showIno = true" ></b-button>
          </div>
        </div>
      </div>
      <div class="game-container">
        <div v-bind:style="computedStyle">
          <div class="stack" v-bind:style="computedStyle">
            <video class="stack" muted loop ref="canvasbg1" v-show="state === 1 && counter < 40"></video>
            <video class="stack" muted loop ref="canvasbg2" v-show="state === 1 && counter > 39"></video>
          </div>
          <div id="konvacanvas" class="stack" v-bind:style="computedStyle"></div>
          <div class="stack startmenu" v-bind:style="computedStyle" v-show="state === 0">
            <div class="center">
              <div class="startmenu-text is-size-4-widescreen is-size-5-desktop is-size-6-touch">
                Click on the hypnotic rings to keep your mind clear.
              </div>
              <div class="startmenu-button center"><b-button type="is-primary" v-on:click="start">Start</b-button></div>
            </div>
          </div>
          <div class="stack rings" v-bind:style="opacity" v-show="state === 1 || state === 2">
            <video muted loop ref="rings"></video>
          </div>
        </div>
        <div class="mid"></div>
        <div v-bind:style="computedStyle">
          <div class="stack" v-bind:style="computedStyle">
            <video muted loop v-show="state === 0" ref="menu"></video>
            <img :src="imageSrcGulp" v-show="state === 1">
            <video v-show="state === 2" ref="gameover"></video>
          </div>
          <div class="stack gameover" v-bind:style="Object.assign({}, computedStyle, {opacity: showGameOver ? 1 : 0})" v-show="state === 2">
            <div class="startmenu-text is-size-2-widescreen is-size-3-desktop is-size-4-touch has-text-weight-bold">
                GAME OVER
            </div>
            <div class="startmenu-text is-size-3-widescreen is-size-4-desktop is-size-5-touch">
                You won't be here in the morning.
            </div>
          </div>
        </div>
      </div>
    </div>

    <b-modal v-model="showIno">
      <div class="modal-card" style="width: auto">
        <header class="modal-card-head">
          <p class="modal-card-title">Cease to Resist v0.4.1 (beta)</p>
          <button type="button" class="delete" v-on:click="showIno = false"/>
        </header>
        <section class="modal-card-body">
          <div class="is-size-5 has-text-weight-bold">Made by Taro</div>
          <div class="is-flex is-flex-direction-row is-align-items-center">
            <b-icon pack="fab" icon="twitter-square" size="is-large"></b-icon>
            <a class="has-text-weight-bold" href="https://twitter.com/Tarolewd" target="_blank">@Tarolewd</a>
          </div>
          <div class="is-flex is-flex-direction-row is-align-items-center">
            <b-icon pack="fab" icon="discord" size="is-large"></b-icon>
            <a class="has-text-weight-bold" href="https://discord.com/invite/V2EKfyDpV6" target="_blank">Kaa's Treetop</a>
          </div>
          <div class="is-size-5 has-text-weight-bold mt-4">Sources</div>
          <div class="content">
            <ul class="mt-0">
              <li>'The Jungle Book' belongs to Disney</li>
              <li>'Kaa ASMR' by <a class="has-text-weight-bold" href="https://www.youtube.com/channel/UCdFPB-7oRVyT74kBaDwid5Q" target="_blank">m5a5e5</a></li>
              <li>'Hypnotic Rings' by <a class="has-text-weight-bold" href="https://www.youtube.com/channel/UC8xGQOErVZSlM3PRHjoMe0A" target="_blank">K VFX</a></li>
            </ul>
          </div>
        </section>
        <footer class="modal-card-foot"></footer>
      </div>
    </b-modal>
  </div>
</template>

<script>
import Konva from 'konva'

export default {
  name: 'App',
  data: function () {
    return {
      bodyWidth: 0,
      bodyHeight: 0,
      konvaCache: {},
      state: 0,
      counter: 0,
      time: 0,
      sounds: {},
      images: {},
      assetsLoaded: 0,
      timeouts: {},
      muted: false,
      showIno: false,
      showGameOver: false,
      isios: false,
      intervall: 1000
    }
  },
  computed: {
    timeMinSec: function () {
      let minutes = Math.floor(this.time / 60)
      let seconds = this.time - minutes * 60
      if (minutes < 10) minutes = `0${minutes}`
      if (seconds < 10) seconds = `0${seconds}`
      return [minutes, seconds]
    },
    dimensions: function () {
      const width = Math.floor(((this.bodyWidth - 35) / 2))
      const height = Math.floor(1072 * (width / 1872))
      return [width, height]
    },
    computedStyle: function () {
      return {
        width: `${this.dimensions[0]}px`,
        height: `${this.dimensions[1]}px`
      }
    },
    opacity: function () {
      let opacity = 0
      if (this.state === 2) {
        opacity = 1
      } else {
        if (this.counter > 19) opacity = (this.counter - 20) * 1 / 90
      }
      return {
        opacity,
        width: `${this.dimensions[0]}px`,
        height: `${this.dimensions[1]}px`
      }
    },
    imageSrcGulp: function () {
      const frame = this.counter > 109 ? 109 : this.counter
      if (this.images.framesGulp && this.images.framesGulp.length > 0) return this.images.framesGulp[frame].src
      return ''
    },
    assetsReady: function () {
      return this.assetsLoaded === 386
    }
  },
  watch: {
    dimensions: function () {
      if (this.konvaCache.stage) {
        this.konvaCache.stage.size({
          width: this.dimensions[0],
          height: this.dimensions[1]
        })
        this.redrawRings()
      }
      if (this.konvaCache.ringsAnimation) {
        if (this.bodyWidth < 1216) {
          if (this.konvaCache.ringsAnimation.isRunning()) this.konvaCache.ringsAnimation.stop()
        } else {
          if (!this.konvaCache.ringsAnimation.isRunning()) this.konvaCache.ringsAnimation.start()
        }
      }
    }
  },
  methods: {
    wait: function (ms) {
      return new Promise(resolve => setTimeout(resolve, ms))
    },
    stopMedia: function (element) {
      element.pause()
      element.currentTime = 0
    },
    updateBodyDimensions: function () {
      this.bodyWidth = document.body.offsetWidth
      this.bodyHeight = document.body.bodyHeight
      this.konvaCache.scale = 1
      if (this.bodyWidth < 1700) this.konvaCache.scale = Math.max(this.bodyWidth / 1700, 0.5)
    },
    initKonva: async function () {
      const stage = new Konva.Stage({
        container: 'konvacanvas',
        width: this.dimensions[0],
        height: this.dimensions[1],
        name: 'stage'
      })
      this.konvaCache.stage = stage

      const layer = new Konva.Layer()
      stage.on('mousedown touchstart', (event) => {
        const target = event.target
        if (target.name().startsWith('rings')) {
          const ringsImage = event.target
          this.konvaCache.ringsCollection.delete(ringsImage.id())
          ringsImage.getParent().destroy()
          layer.batchDraw()
          this.counter--
        }
      })
      stage.add(layer)
      this.konvaCache.layer = layer

      const ringsGroup = new Konva.Group({
        width: 80,
        height: 80,
        clipFunc: function (ctx) {
          ctx.arc(40, 40, 39, 0, Math.PI * 2, false)
        },
        name: 'rings-group'
      })
      this.konvaCache.ringsGroup = ringsGroup

      const ringsImage = new Konva.Image({
        width: 80,
        height: 80,
        name: 'rings'
      })
      this.konvaCache.ringsImage = ringsImage

      const ringsAnimation = new Konva.Animation(() => {
        const now = Date.now()
        if (now - this.konvaCache.lastFrameUpdate >= 65) {
          this.konvaCache.ringsCollection.forEach(value => {
            value.currentFrame = (value.currentFrame + 1) % 266
            value.ringsImage.image(this.images.framesRings[value.currentFrame])
          })
          this.konvaCache.lastFrameUpdate = now
        } else {
          return false
        }
      }, layer)
      this.konvaCache.ringsAnimation = ringsAnimation

      this.konvaCache.ringsIdCounter = 0
      this.konvaCache.ringsCollection = new Map()
    },
    initImages: async function () {
      this.images.framesRings = []
      const framesRingsData = (await import('../src/assets/images/frames-rings.json')).default
      framesRingsData.forEach(frameRingsData => {
        const image = new Image()
        image.addEventListener('load', () => this.assetsLoaded++, { once: true })
        image.src = frameRingsData
        this.images.framesRings.push(image)
      })
      this.images.framesGulp = []
      const framesHypnoData = (await import('../src/assets/images/frames-hypno.json')).default
      framesHypnoData.forEach(frameHypnoData => {
        const image = new Image()
        image.addEventListener('load', () => this.assetsLoaded++, { once: true })
        image.src = frameHypnoData
        this.images.framesGulp.push(image)
      })
      const framesGulpData = (await import('../src/assets/images/frames-gulp.json')).default
      framesGulpData.forEach(frameGulpData => {
        const image = new Image()
        image.addEventListener('load', () => this.assetsLoaded++, { once: true })
        image.src = frameGulpData
        this.images.framesGulp.push(image)
      })
    },
    initSounds: function () {
      ['gulp', 'ping', 'asmr', 'doom', 'gameover'].forEach(soundName => {
        const sound = new Audio()
        sound.preload = 'auto'
        sound.addEventListener('canplaythrough', () => this.assetsLoaded++, { once: true })
        sound.volume = 0
        sound.loop = soundName === 'asmr'
        sound.src = require(`../src/assets/sounds/${soundName}.mp3`)
        sound.load()
        this.sounds[soundName] = sound
      })
      this.isios = this.sounds.gulp.volume !== 0
    },
    initVideos: function () {
      ['menu', 'rings', 'gameover', 'canvasbg1', 'canvasbg2'].forEach(videoName => {
        const video = this.$refs[videoName]
        video.preload = 'auto'
        video.addEventListener('canplaythrough', () => this.assetsLoaded++, { once: true })
        if (videoName === 'gameover') {
          video.addEventListener('ended', () => {
            this.sounds.gameover.play()
            this.showGameOver = true
          })
        }
        video.autoplay = videoName === 'menu'
        const source = document.createElement('source')
        source.setAttribute('src', require(`../src/assets/videos/${videoName}.mp4`))
        source.setAttribute('type', 'video/mp4')
        video.appendChild(source)
        video.load()
      })
    },
    addRings: function () {
      const ringsGroupCLone = this.konvaCache.ringsGroup.clone({
        id: `group-${this.konvaCache.ringsIdCounter}`,
        scaleX: this.konvaCache.scale,
        scaleY: this.konvaCache.scale,
        x: Math.random() * (this.konvaCache.stage.width() - 80 * this.konvaCache.scale),
        y: Math.random() * (this.konvaCache.stage.height() - 80 * this.konvaCache.scale)
      })
      const ringsImageId = `group-${this.konvaCache.ringsIdCounter}-image`
      const startFrame = Math.floor(Math.random() * 266)
      const ringsImageClone = this.konvaCache.ringsImage.clone({
        id: ringsImageId,
        image: this.images.framesRings[startFrame]
      })
      ringsGroupCLone.add(ringsImageClone)
      this.konvaCache.layer.add(ringsGroupCLone)
      this.konvaCache.layer.batchDraw()
      this.konvaCache.ringsCollection.set(ringsImageId, {
        currentFrame: startFrame,
        ringsImage: ringsImageClone
      })
      this.konvaCache.ringsIdCounter++
      this.counter++
    },
    redrawRings: function () {
      const ringsGroups = this.konvaCache.layer.getChildren()
      ringsGroups.each(ringsGroup => {
        ringsGroup.scaleX(this.konvaCache.scale)
        ringsGroup.scaleY(this.konvaCache.scale)
        ringsGroup.x(Math.random() * (this.konvaCache.stage.width() - 80 * this.konvaCache.scale))
        ringsGroup.y(Math.random() * (this.konvaCache.stage.height() - 80 * this.konvaCache.scale))
      })
      this.konvaCache.layer.batchDraw()
    },
    running: function () {
      if (this.state !== 1) return
      if (this.time > 0 && this.time % 5 === 0) {
        this.intervall = 1000 - Math.floor(300 * (Math.log(this.time / 5 + 1) / Math.log(3)))
      }
      this.timeouts.running = setTimeout(() => {
        if (this.state !== 1) return
        this.addRings()
        if (this.counter < 110) {
          if (this.counter === 92) this.sounds.gulp.play()
          if (this.counter > 58) this.sounds.asmr.volume = 0.7 - 0.013 * (this.counter - 59)
          if (this.counter > 39) {
            this.$refs.canvasbg2.play()
            this.stopMedia(this.$refs.canvasbg1)
          }
          this.running()
        } else {
          this.konvaCache.ringsAnimation.stop()
          this.stopMedia(this.$refs.canvasbg2)
          this.stopMedia(this.sounds.asmr)
          this.state = 2
          this.gameOverSequence()
        }
      }, this.intervall)
    },
    gameOverSequence: async function () {
      this.sounds.ping.play()
      await this.wait(2000)
      this.$refs.gameover.play()
    },
    timer: function () {
      this.timeouts.timer = setTimeout(() => {
        if (this.state !== 1) return
        if (this.counter < 110) {
          this.timer()
          this.time++
        }
      }, 1000)
    },
    start: function () {
      if (!this.isios) {
        this.$refs.gameover.volume = 0
        this.$refs.gameover.play().then(() => {
          this.stopMedia(this.$refs.gameover)
          this.$refs.gameover.volume = 0.9
        }).catch(() => null)
        const soundsArray = Object.entries(this.sounds)
        for (let i = 0; i < soundsArray.length; i++) {
          const [name, sound] = soundsArray[i]
          sound.play().then(() => {
            if (name !== 'asmr') this.stopMedia(sound)
            sound.volume = name === 'asmr' ? 0.7 : 1
          }).catch(() => null)
        }
      }
      this.stopMedia(this.$refs.menu)
      this.$refs.rings.play()
      this.$refs.canvasbg1.play()
      this.state = 1
      this.addRings()
      this.konvaCache.lastFrameUpdate = Date.now()
      if (this.bodyWidth > 1215) this.konvaCache.ringsAnimation.start()
      this.running()
      this.timer()
    },
    reset: function () {
      if (this.timeouts.running) {
        clearTimeout(this.timeouts.running)
        this.timeouts.running = null
      }
      if (this.timeouts.timer) {
        clearTimeout(this.timeouts.timer)
        this.timeouts.timer = null
      }
      this.konvaCache.layer.destroyChildren()
      this.konvaCache.layer.batchDraw()
      this.stopMedia(this.$refs.canvasbg1)
      this.stopMedia(this.$refs.canvasbg2)
      this.stopMedia(this.$refs.rings)
      this.stopMedia(this.$refs.gameover)
      this.stopMedia(this.sounds.asmr)
      this.stopMedia(this.sounds.gameover)
      this.sounds.asmr.volume = 0.7
      this.$refs.menu.play()
      this.counter = 0
      this.time = 0
      this.intervall = 1000
      this.konvaCache.ringsIdCounter = 0
      this.konvaCache.ringsCollection = new Map()
      this.showGameOver = false
      this.state = 0
    },
    mute: function () {
      this.$refs.gameover.muted = true
      Object.values(this.sounds).forEach(sound => {
        sound.muted = true
      })
      this.muted = true
    },
    unmute: function () {
      this.$refs.gameover.muted = false
      Object.values(this.sounds).forEach(sound => {
        sound.muted = false
      })
      this.muted = false
    }
  },
  mounted: async function () {
    this.updateBodyDimensions()
    window.addEventListener('resize', this.updateBodyDimensions)
    this.initImages()
    this.initSounds()
    this.initVideos()
    this.initKonva()
    if (this.isios) {
      this.$buefy.dialog.alert({
        title: 'Limited browser support',
        message: 'Some features (e.g. audio, Game Over sequence) are not available.',
        type: 'is-danger',
        ariaRole: 'alertdialog',
        ariaModal: true
      })
    }
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.updateBodyDimensions)
  }
}
</script>

